@import "./variable.scss";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Lato", sans-serif;
  font-size: 62.5%;

  &::-webkit-scrollbar {
    background-color: transparent;
    width: 6px;
    overflow-y: overlay;
  }

  &::-webkit-scrollbar-thumb {
    background-color: rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    overflow-y: overlay;
  }
}
.App {
}
a {
  text-decoration: none;
  color: $text-color;
  display: block;
}

button {
  border: none;
  outline: none;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.margin-top {
  margin-top: 90px;
}

.scroll-top {
  font-size: 4rem;
  position: fixed;
  bottom: 40px;
  right: 30px;
  cursor: pointer;
  transition: all ease 0.2s;

  &:hover {
    color: #f29e33;
    transition: all ease 0.2s;
  }

  &.show {
    bottom: 40px;
    opacity: 1;
    animation: appear linear 0.6s forwards;
  }

  &.hidden {
    bottom: -4px;
    animation: disappear linear 0.6s forwards;
  }

  @include tablet {
    right: 20px;
  }

  @include mobile {
    right: 10px;
  }
}

@keyframes disappear {
  0% {
    bottom: 42px;
    opacity: 1;
  }
  25% {
    bottom: 46px;
  }
  50% {
    bottom: 42px;
    opacity: 0.6;
  }
  75% {
    bottom: 50px;
    opacity: 0, 4;
  }
  100% {
    bottom: -10px;
    opacity: 0;
  }
}

@keyframes appear {
  0% {
    bottom: -10px;
    opacity: 0;
  }
  50% {
    bottom: 52px;
    opacity: 0, 4;
  }
  100% {
    bottom: 42px;
    opacity: 1;
  }
}
